@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: "Poppins", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

::-webkit-scrollbar {
  width: 0.5rem;
  background-color: transparent;
}

/* Track */
::-webkit-scrollbar-track {
  background-color: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #19398a;
  border-radius: 0px;
  height: 15rem;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
@media (max-width: 1920px) {
  html {
    font-size: 21.8px;
  }
}
@media (max-width: 1600px) {
  html {
    font-size: 18px;
  }
}
@media (max-width: 1366px) {
  html {
    font-size: 14.5px;
  }
}
@media (max-width: 1280px) {
  html {
    font-size: 14.5px;
  }
}
@media (max-width: 1200px) {
  html {
    font-size: 11.6px;
  }
}
@media (max-width: 1000px) {
  html {
    font-size: 9.6px;
  }
}
@media (max-width: 800px) {
  html {
    font-size: 8.3px;
  }
}
@media (max-width: 700px) {
  html {
    font-size: 7.5px;
  }
}
@media (max-width: 650px) {
  html {
    font-size: 7px;
  }
}
@media (max-width: 600px) {
  html {
    font-size: 5.7px;
  }
}
@media (max-width: 500px) {
  html {
    font-size: 13px;
  }
}

.App {
  text-align: center;
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  align-content: center;
  background-color: #eff0f5;
}
.app-space {
  width: 100%;
  height: calc(100vh - 3rem);
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-content: flex-start;
  align-items: flex-start;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 1rem;
  box-sizing: border-box;
  overflow: hidden;
}
@media (max-width: 500px) {
  .app-space {
    padding-right: 0.3rem;
    padding-top: 0.3rem;
    padding-left: 0.3rem;
  }
}

.loader_loader__35zv6 {
  border: 0.5rem solid #f3f3f3;
  border-radius: 50%;
  border-top: 0.5rem solid #19398a;
  width: 3rem;
  height: 3rem;
  -webkit-animation: loader_spin__2vwQ0 2s linear infinite; /* Safari */
  animation: loader_spin__2vwQ0 2s linear infinite;
}

/* Safari */
@-webkit-keyframes loader_spin__2vwQ0 {
  0% {
    -webkit-transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes loader_spin__2vwQ0 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

